<script>
  import Search from '@/components/Search'
  import HotOrder from '@/components/HotOrder'
  import api from "@/api/home-api"
  import commonApi from "@/api/common-api"
  export default {
      name: "index.vue",
      data(){
          return{
            adve:'',
            data:{group:{image:[],brand:[]},brand:[]},
            cate:''
          }
      },
      created(){

      },
     mounted () {
      console.log("mounted");
       this.getData();
       this.getCate();
       this.getAdve();
    },
    beforeRouteUpdate (to, from, next) {
      // 在当前路由改变，但是该组件被复用时调用
      // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
      // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
      // 可以访问组件实例 `this`
      // console.log("created");
      // this.getData();
      // this.getCate();
      // this.getAdve();
    },
      methods:{
          /**
           * 获取页面数据
           */
         getData : function (){
           api.getData().then((data)=>{
             this.data = data;
          })

         },

        /**
         * 获取所有广告
         */
        getAdve: function(){
          commonApi.getAdve({wp:1}).then((adve)=>{
            this.adve = adve;
          });
        },

        /**
         * 获取所有分类
         */
        getCate: function(){
          api.getCate().then((cate)=>{
            this.cate = cate;
            console.log("cate",cate);
          });
        }


      },
      components: {
        Search,
        HotOrder
      }
    }
</script>

